@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');

[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    box-sizing: border-box;
    width: 171px;
    height: 36px;
    border: 1px solid #C2C2C2;
    border-radius: 2px;
}
